#site-header {
    border-bottom: 1px solid transparent !important; }

#page-content {
    padding-top: 0 !important; }

#article-header {
    display: block;
    position: relative;
    float: left;
    margin-bottom: 20px;
    width: 100%;

    &:after {
        position: absolute;
        z-index: -1;
        opacity: 0.95;
        content: "";
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        background-color: #333;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center; } }

#article-body {
    float: left;
    width: 100%;
    height: auto; }

#article-body .center {
    width: 750px !important; }

#article-body img {
    display: block;
    margin: 0 auto; }

#article-title {
    float: left;
    width: 100%;
    font-size: 1.3em;
    text-align: center; }

#article-title h1 {
    margin: 90px 0 90px 0;
    font-weight: 400; }

#article-title h1 span {
    padding: 15px 25px;
    background-color: #111;
    color: #FAFAFA;
    border-radius: 7px;
    -moz-border-radius: 7px;
    -webkit-border-radius: 7px; }

#article-meta {
    float: left;
    padding: 0px 0px 20px 0px;
    width: 100%;
    font-size: 0.85em; }

#article-time {
    float: left;
    padding-top: 5px; }

#article-share {
    float: right;
    margin-right: 15px;
    font-size: 1.75em; }

#article-share a {
    text-decoration: none; }

#article-content {
    float: left;
    width: 100%; }

#article-content p {
    max-width: 100%;
    overflow: hidden;
    text-overflow: ellipsis; }

.important {
    padding: 20px;
    border: 2px solid #EE0000;
    border-radius: 7px;
    -moz-border-radius: 7px;
    -webkit-border-radius: 7px; }

#bibliography,
#bibliography > div {
    float: left;
    width: 100%; }

#bibliography {
    max-width: 100%;
    word-wrap: break-word; }

#bibliography > div {
    padding: 10px 0;
    font-size: 0.85em; }

#bibliography > div:not(:last-of-type) {
    border-bottom: 1px solid #D5D5D5; }


@media screen and (max-width: 700px) {
    #article-title {
        font-size: 1em; }

    #article-title h1 {
        margin: 70px 0 70px 0;
        line-height: 1.75em; }

    #article-title h1 span {
        padding: 15px 5px; } }
